body {
  /*font-family     : "Segoe UI", "Segoe WP", Segoe, device-segoe, Tahoma, Helvetica, Arial, sans-serif !important;*/
  padding: 0;
  color: var(--vscode-editor-foreground);
  background-color: inherit !important;
}
html,
body,
#test-list-container,
#test-entry-form,
#main-content {
  margin: 0;
  height: 100%;
}
.navbar-brand {
  color: var(--vscode-editor-foreground) !important;
}
#menubar {
  box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.2);
  background-color: var(--vscode-tab-activeBackground) !important;
}
#menubar ul.navbar-nav li {
  margin-right: 10px;
}
#main-content {
  padding-top: 50px;
}
#main-content .row,
#main-content .test-col {
  margin: 0;
  height: 100%;
  overflow: auto;
}
ul#sidebar-list {
  margin: 20px 0 0;
  padding: 0;
  list-style: none;
}
ul#sidebar-list li {
  list-style: none;
  margin: 0;
}
ul#sidebar-list li div {
  padding: 7px;
  cursor: pointer;
}
ul#sidebar-list li div:not(.active) {
  background-color: transparent;
}
ul#sidebar-list li div:hover:not(.active) {
  background-color: var(--vscode-button-hoverBackground);
}
ul#sidebar-list li div.active {
  background-color: var(--vscode-button-background);
}
ul#sidebar-list li ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
ul#sidebar-list li ul li {
  margin: 0;
}
ul#sidebar-list li ul li div {
  padding: 7px 7px 7px 15px;
}
.gutter-horizontal {
  background-color: var(--vscode-scrollbarSlider-background);
  cursor: col-resize;
}
#test-entry-form,
#test-list-container {
  overflow: auto;
  border-bottom-width: 0px !important;
}
.custom-select {
  background-image: none;
}
.card {
  color: var(--vscode-editor-foreground) !important;
  border-color: var(--vscode-panel-border) !important;
  background-color: transparent;
}
.input-group-text,
.form-control {
  color: var(--vscode-input-foreground) !important;
  background-color: var(--vscode-input-background) !important;
  border-color: var(--vscode-panel-border) !important;
}
.input-group-text:focus,
.form-control:focus {
  color: var(--vscode-input-foreground) !important;
  background-color: var(--vscode-input-background) !important;
  border-color: var(--vscode-input-border) !important;
}
::placeholder {
  color: var(--vscode-input-placeholderForeground) !important;
}
.ag-theme-material {
  color: var(--vscode-editor-foreground);
  font-size: 0.875rem;
  background-color: inherit;
}
.ag-theme-material .ag-header {
  text-transform: uppercase;
  font-size: 0.875rem;
  font-weight: 400;
  background: inherit;
  color: var(--vscode-editor-foreground) !important;
  border-color: var(--vscode-panel-border) !important;
}
.ag-theme-material .ag-row {
  border-color: var(--vscode-panel-border) !important;
}
.ag-theme-material .ag-header-cell,
.ag-theme-material .ag-cell {
  padding-left: 5px;
  padding-right: 5px;
  border-bottom-color: none !important;
  /*var(--vscode-panel-border) !important;*/
}
.ag-theme-material .ag-cell-inline-editing {
  height: inherit;
}
.ag-theme-material .ag-cell-inline-editing input[type="text"]:focus,
.ag-theme-material .ag-cell-inline-editing input[type="tel"]:focus,
.ag-theme-material .ag-cell-inline-editing input[type="date"]:focus,
.ag-theme-material .ag-cell-inline-editing input[type="datetime-local"]:focus {
  border-color: var(--vscode-panel-border) !important;
}
.ag-theme-material .ag-popup-editor input[type="text"]:focus,
.ag-theme-material .ag-popup-editor input[type="tel"]:focus,
.ag-theme-material .ag-popup-editor input[type="date"]:focus,
.ag-theme-material .ag-popup-editor input[type="datetime-local"]:focus {
  border-color: var(--vscode-panel-border) !important;
}
.ag-theme-material .ag-header-cell:hover,
.ag-theme-material .ag-row-hover {
  background: var(--vscode-button-hoverBackground) !important;
}
.ag-theme-material .ag-row-selected {
  background: var(--vscode-button-background) !important;
}
.ag-theme-material .ag-row-selected .ag-row-hover {
  background: var(--vscode-button-hoverBackground) !important;
}
.ag-cell.ag-cell-with-height.ag-cell-value.ag-cell-focus.ag-cell-inline-editing,
.ag-has-focus .ag-cell-focus {
  border-color: var(--vscode-input-border) !important;
}
body.vscode-light ul#sidebar-list li div:not(.active) {
  background-color: transparent;
}
body.vscode-light ul#sidebar-list li div:hover:not(.active) {
  background: #dbf5f7 !important;
}
body.vscode-light ul#sidebar-list li div.active {
  background: #b2e9ed !important;
}
body.vscode-light .btn-outline-primary:hover,
body.vscode-light .btn-outline-secondary:hover,
body.vscode-light .btn-outline-success:hover,
body.vscode-light .btn-outline-primary:active,
body.vscode-light .btn-outline-secondary:active,
body.vscode-light .btn-outline-success:active {
  color: #fff !important;
}
body.vscode-light .ag-header-cell:hover,
body.vscode-light .ag-row-hover {
  background: #dbf5f7 !important;
}
body.vscode-light .ag-row-selected {
  background: #b2e9ed !important;
}
body.vscode-light .ag-row-selected .ag-row-hover {
  background: #b2e9ed !important;
}
body.vscode-high-contrast .ag-theme-material a,
body.vscode-dark .ag-theme-material a,
body.vscode-high-contrast .ag-theme-material a:link,
body.vscode-dark .ag-theme-material a:link,
body.vscode-high-contrast .ag-theme-material a:hover,
body.vscode-dark .ag-theme-material a:hover,
body.vscode-high-contrast .ag-theme-material a:focus,
body.vscode-dark .ag-theme-material a:focus {
  color: var(--vscode-editor-foreground) !important;
  text-decoration: underline;
}
body.vscode-high-contrast #menubar,
body.vscode-dark #menubar {
  box-shadow: none;
  border-bottom: 1px solid var(--vscode-panel-border) !important;
}
body.vscode-high-contrast #menubar .input-group-text,
body.vscode-dark #menubar .input-group-text,
body.vscode-high-contrast #menubar .form-control,
body.vscode-dark #menubar .form-control {
  border-color: var(--vscode-input-placeholderForeground) !important;
}
body.vscode-high-contrast .btn-outline-primary,
body.vscode-dark .btn-outline-primary,
body.vscode-high-contrast .btn-outline-secondary,
body.vscode-dark .btn-outline-secondary,
body.vscode-high-contrast .btn-outline-success,
body.vscode-dark .btn-outline-success {
  color: #fff !important;
}
